import * as React from "react";
import Avatar from "@mui/material/Avatar";
import Button from "@mui/material/Button";
import CssBaseline from "@mui/material/CssBaseline";
import TextField from "@mui/material/TextField";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";

import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import LockOutlinedIcon from "@mui/icons-material/LockOutlined";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";
import "../Login/Login.css";
import { useState } from "react";
import axios from "axios";
function Copyright(props) {
  return (
    <Typography
      variant="body2"
      color="text.secondary"
      align="center"
      {...props}
    >
      {"Copyright © "}
      <Link color="inherit" href="https://mui.com/">
        Your Website
      </Link>
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

const theme = createTheme();

export default function SignIn() {
  // validation
  const [emailerror, setEmailerror] = useState("");
  const [passworderror, setPassworderror] = useState("");
  const [usererror, setUsererror] = useState("");
  const [successMsg, setSuccessMsg] = useState("");
  const [isLoggedin, setIsLoggedin] = useState(false);
  const handleEmailchange = (e) => {
    setSuccessMsg("");
    setEmailerror("");
    setEmail(e.target.value);
    localStorage.setItem("token-info", JSON.stringify(handleSubmit));
    setIsLoggedin(true);
  };

  const handlePasswordchange = (e) => {
    setSuccessMsg("");
    setPassworderror("");
    setPassword(e.target.value);
  };

  const accesToken =
    "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9..OPH6ne14P-u9vSyj3zh5TwCKm1nTB2V1YosfRXzYzsU";

  const authAxios = axios.create({
    baseURL: "https://rslsofttech.com:5000/admin/Login",
    headers: {
      Authorization: `bearer ${accesToken}`,
    },
  });

  const handleUserchange = (e) => {
    setSuccessMsg("");
    setUsererror("");
    setUser(e.target.value);
  };

  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [username, setUser] = useState("");
  const navigate = useNavigate();

  const handleSubmit = (event) => {
    event.preventDefault();

    // checking if email is empty
    if (email !== "") {
      // check some other condtion
      const emailRegex =
        /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
      if (emailRegex.test(email)) {
        setEmailerror("");
       
        if (email === "shwetapanchal@gmail.com") {
          setEmailerror("");
          if (password === "shwetapanchal") {
            setSuccessMsg("You are successfully logged in");
          } else {
            setPassworderror("Password does not match with the email address");
          }
        } else {
          setEmailerror("email does not match with our database");
        }
      } else {
        setEmailerror("Invailid Email");
      }
    } else {
      setEmailerror("Email Required");
    }
    // checking if password is empty
    if (password !== "") {
      // do something here
    } else {
      setPassworderror("Password Required");
    }

    // checking if username is empty
    if (usererror !== "") {
      // do something here
    } else {
      setUsererror("Username Required");
    }

    // event.target.reset();

    authAxios
      .post("https://rslsofttech.com:5000/admin/Login", {
        username: username,
        email: email,
        password: password,
      })
      .then((result) => {
        console.log(result);
        localStorage.setItem("token", result.data.token);
        navigate("/");
       
      })
      .catch((error) => {
        console.log(error);
      });

    // const data = new FormData(event.currentTarget);
  };

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline />
        <Box
          className="login"
          sx={{
            p: 2,
            marginTop: 15,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Avatar sx={{ m: 1, bgcolor: "secondary.main" }}>
            <LockOutlinedIcon />
          </Avatar>
          <Typography component="h1" variant="h5">
            Login
          </Typography>
          <Box component="form" onSubmit={handleSubmit} sx={{ mt: 1 }}>
            {successMsg && (
              <Typography variant="text" className="success-msg">
                {successMsg}
              </Typography>
            )}
            <TextField
              margin="normal"
              fullWidth
              id="username"
              label="User Name"
              name="username"
              autoComplete="username"
              autoFocus
              onChange={handleUserchange}
            />
            {usererror && (
              <Typography variant="text" className="error-msg">
                {usererror}
              </Typography>
            )}

            <TextField
              margin="normal"
              fullWidth
              id="email"
              label="Email"
              name="email"
              autoComplete="email"
              autoFocus
              onChange={handleEmailchange}
            />
            {emailerror && (
              <Typography variant="text" className="error-msg">
                {emailerror}
              </Typography>
            )}

            <TextField
              margin="normal"
              // required
              fullWidth
              name="password"
              label="Password"
              type="password"
              id="password"
              autoFocus
              onChange={handlePasswordchange}
              // autoComplete="current-password"
            />
            {passworderror && (
              <Typography variant="text" className="error-msg">
                {passworderror}
              </Typography>
            )}

            <Box sx={{ textAlign: "center" }}>
              <Button
                type="submit"
                fullWidth
                variant="contained"
                sx={{ mt: 3, mb: 2, width: "40%" }}
              >
                Login
              </Button>
            </Box>
            {/* <Grid container justifyContent="flex-end">
              <Grid item>
                <Link to="register" variant="body2">
                Don't have an account? Sign Up
                </Link>
              </Grid>
            </Grid> */}
          </Box>
        </Box>
      </Container>
    </ThemeProvider>
  );
}
